import * as twgl from "./../node_modules/twgl.js";    // Greg's work
import { m4 } from "./../node_modules/twgl.js";

import * as mtls from "./mouselistener";     // connect events for buttons and wheel
import * as camhandler from "./camhandler"   // camera projection
import * as stridedmesh0 from "./stridedmesh0" // mesh and bones (data)

import * as boneanimation from "./boneanimation"
import * as fish from "./fish"

//-------------------------------------------------------------------------------------------------------------------------------------------------------------

twgl.setAttributePrefix("a_");

type Tuniforms = {
    world: m4.Mat4,
    projection: m4.Mat4,
    worldviewprojection: m4.Mat4;
    view: m4.Mat4,
    surfaceTexture: WebGLTexture,
    boneMatrixTexture: WebGLTexture,
    color: number[]
  };

export class Skeleton extends fish.FishHTranslated
{
    gl: WebGL2RenderingContext;
    app: mtls.MouseListener;   
    cam: camhandler.Camera;
    uniforms: Tuniforms;
    mesh: stridedmesh0.StridedMesh0;   
    programInfo: twgl.ProgramInfo;
     
    bufferInfo: twgl.BufferInfo | null = null;
    skinVAO: WebGLVertexArrayObject | null = null;

    phase0: number=0.0; //2.0; // 143 degrees 
   
    constructor( cgl: WebGL2RenderingContext, capp: mtls.MouseListener | undefined , dictpar:Map<string,string>)
    { 
      
      super(1.0, 0.03, 1.0, 0.015,0.5,2.5, "zelenskyy");
      var spar:string|undefined;
      if ((spar=dictpar.get("phase2"))!=undefined) this.phase0= +spar!;
    
      this.gl = cgl;
      this.app = capp!;
      this.prepareSurfaceTextures(this.gl, "zelenskyy");
      this.programInfo = twgl.createProgramInfo(this.gl, [boneanimation.vsSkeleton, boneanimation.fsSkeleton]);
      this.mesh = this.prepareMesh(this.gl, dictpar, 1.0);
      
      this.numBones = (this.mesh.type==this.gl.TRIANGLE_STRIP)? (this.mesh!.nsegments / this.mesh!.bonediv) : this.mesh!.nsegments; 
     
      this.createBoneTexture(this.gl, dictpar);
      this.createSurfaceTexture(this.gl);
      this.uniforms= this.prepareUniforms(this.gl, dictpar); // this.phase0);
      
      this.bufferInfo = twgl.createBufferInfoFromArrays(this.gl, this.mesh!.arrays);
      this.skinVAO = twgl.createVAOFromBufferInfo(this.gl, this.programInfo, this.bufferInfo);
       
      this.cam=camhandler.Camera.createZUpCamera(this.gl,dictpar,50.0, this.app);
      this.cam.zoominVelocity = 0.5;
      requestAnimationFrame(() => this.render(0));    
    }

    //------------------------------------------------------------------------------------------------------------------------------------

    render(time: number) 
    {
        //time=0;
        var gl = this.gl;
        gl.useProgram(this.programInfo!.program);
        twgl.resizeCanvasToDisplaySize(gl.canvas);
        gl.viewport(0, 0,  gl.canvas.width, gl.canvas.height);        
        gl.enable(gl.DEPTH_TEST);
        gl.enable(gl.CULL_FACE);
        gl.clearColor(0.1, 0.1, 0.1, 1.0);       
        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
      
        var cam: camhandler.Camera = this.cam;
        cam.CamHandlingZUp(gl, this.app);
        this.uniforms.worldviewprojection = cam.viewProjection;   
  
        gl.bindVertexArray(this.skinVAO);
    
        this.computeBone(time);
        this.prepareBoneTexture(gl,this.bindPoseInv2); 
       
        this.uniforms.world = m4.translate(m4.identity(), [20.0, -20.0, 0.0]);  // draw a fish
        twgl.setUniforms(this.programInfo!, this.uniforms)
        twgl.drawBufferInfo(gl, this.bufferInfo!, this.mesh!.type);     
       
        this.uniforms.world = m4.translate(m4.identity(), [0.0, 0.0, 0.0]);     // draw a fish
        twgl.setUniforms(this.programInfo!, this.uniforms)
        twgl.drawBufferInfo(gl, this.bufferInfo!, this.mesh!.type);
  
        this.computeBone(time);
        this.prepareBoneTexture(gl,this.bindPoseInv2);

        this.uniforms.world = m4.translate(m4.identity(), [50.0, -20.0, 10.0]); // draw a fish    
        twgl.setUniforms(this.programInfo!, this.uniforms)
        twgl.drawBufferInfo(gl, this.bufferInfo!, this.mesh!.type);     

        this.uniforms.world = m4.translate(m4.identity(), [-10.0, 5.0, -10.0]); // draw a fish
        twgl.setUniforms(this.programInfo!, this.uniforms)
        twgl.drawBufferInfo(gl, this.bufferInfo!, this.mesh!.type);
       
        requestAnimationFrame(() => this.render(++time));       
    }   
}
